body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: black;
  margin: none;
  height: 100vh;
  max-height: 100vh;
  margin: none;
}
h1 {
  opacity: 0;
  font-size: 100px;
  color: white;
  overflow: hidden;
}
.notsure {
  left: 0vw;
  position: fixed;
  margin: 0;
}
@media only screen and (min-width: 1001px) {
  .notsure {
    -webkit-animation: notsure1 9s 1;
            animation: notsure1 9s 1;
  }
}
@media only screen and (max-width: 1000px) {
  .notsure {
    -webkit-animation: notsure2 9s 1;
            animation: notsure2 9s 1;
  }
}
@media only screen and (max-width: 800px) {
  .notsure {
    -webkit-animation: notsure3 9s 1;
            animation: notsure3 9s 1;
    text-align: center;
    margin: 0;
    left: calc(50vw - 193px);
  }
}
@media only screen and (max-width: 450px) {
  .notsure {
    -webkit-animation: notsure3 9s 1;
            animation: notsure3 9s 1;
    text-align: center;
    margin: 0;
    left: calc(50vw - 154px);
    font-size: 80px;
  }
}
.ru {
  text-align: center;
  margin: 0;
  padding-top: 20vh;
  -webkit-animation: ru 16s 1;
          animation: ru 16s 1;
}
img {
  opacity: 0;
  position: absolute;
  width: 100px;
  -webkit-animation: think 4s infinite;
          animation: think 4s infinite;
}
